  --text-input-height: 48px;
  --ingredients-item-height: 2rem;
  --ingredients-gap: 0.5rem;
  --transition-popup: 0.3s cubic-bezier(0.3, 0, 0.2, 1);
  --transition-popup-open: 120ms ease-out;
}

.popup {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: fixed;
  inset: 0;
  z-index: 100;
  height: 100%;
  pointer-events: none;
}

.popup_opened {
  pointer-events: auto;
}
.popup_opened .popup__overlay {
  opacity: 1;
}
.popup_opened .popup__content {
  transform: translateY(0);
}

.popup__overlay {
  position: fixed;
  inset: 0;
  background: var(--color-bg-overlay);
  opacity: 0;
  transition: opacity var(--transition-popup);
}

.popup__content {
  position: relative;
  overflow: auto;
  width: 100%;
  height: auto;
  max-height: calc(100% - 3rem);
  background: var(--color-bg-secondary);
  transform: translateY(100%);
  border-radius: var(--border-radius-400) var(--border-radius-400) 0 0;
  scroll-behavior: smooth;
  transition: transform var(--transition-popup);
}

/* -------------------------------------------------------------------------- */
/*                                   SEARCH                                   */
/* -------------------------------------------------------------------------- */
.ingredients-search {
  display: flex;
  flex-direction: column;
  row-gap: 0.5rem;
  width: 100%;
}

.ingredients-search__form {
  display: flex;
  flex-direction: column;
  position: relative;
  height: var(--text-input-height);
  background: var(--color-bg-tertiary);
  border-radius: var(--border-radius-200);
}

.ingredients-search__input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  background: transparent;
  padding: 0 0.8rem;
  font: inherit;
  color: inherit;
}
.ingredients-search__input:focus {
  outline: none;
}

.ingredients-search__buttons {
  width: 100%;
}

/* -------------------------------------------------------------------------- */
/*                        INGREDIENT SEARCH SUGGESTIONS                       */
/* -------------------------------------------------------------------------- */
.ingredients-search__suggestions {
  display: none;
  position: absolute;
  top: calc(100% + 0.5rem);
  overflow: hidden;
  z-index: 10;
  width: 100%;
  background: var(--color-bg-secondary);
  font: var(--font-type-body);
  border-radius: var(--border-radius-200);
}

.ingredients-search__suggestions_show {
  display: block;
}

.ingredients-search__suggestions-list {
  max-height: calc(var(--text-input-height) * 5);
  margin: 0;
  padding: 0;
  overflow: auto;
  text-indent: 0;
  list-style-type: none;
}

.ingredients-search__suggestions-item {
  display: flex;
  align-items: center;
  padding: 0 0.8rem;
  height: var(--text-input-height);
}
.ingredients-search__suggestions-item_active {
  background: var(--color-bg-tertiary);
}

/* -------------------------------------------------------------------------- */
/*                                 INGREDIENTS                                */
/* -------------------------------------------------------------------------- */
.ingredients {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding: 0 1.5rem;
}

.ingredients-category_active .arrow-icon {
  transform: rotateX(180deg);
}
.ingredients-category_active .ingredients-item:nth-child(n+8) {
  display: flex;
}
.ingredients-category_active .ingredients-item-more-btn.ingredients-item-more-btn {
  display: none;
}

.ingredients-category__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 50px;
}

.ingredients-category__title {
  font: var(--font-type-h3);
}

.ingredients-category__body {
  margin-top: 0.5rem;
}

.ingredients-list {
  display: flex;
  flex-wrap: wrap;
  gap: var(--ingredients-gap);
  overflow: hidden;
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style-type: none;
}

.ingredients-item {
  display: flex;
  align-items: center;
  height: var(--ingredients-item-height);
  padding: 0 0.7rem;
  background: var(--color-bg-tertiary);
  border-radius: var(--border-radius-200);
}
.ingredients-item-product_active {
  background: var(--color-bg-highlight);
  color: var(--color-text-highlight);
}
.ingredients-item:nth-child(n+9) {
  display: none;
}
.ingredients-item_size_big {
  padding: 0 1.2rem;
}

/* -------------------------------------------------------------------------- */
/*                          SELECTED INGREDIENTS MENU                         */
/* -------------------------------------------------------------------------- */
.selected-ingredients-menu {
  width: 100%;
  border: 2px solid var(--color-bg-tertiary);
  border-radius: var(--border-radius-200);
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
.selected-ingredients-menu_active .selected-ingredients-menu__body {
  grid-template-rows: 1fr;
  border-width: 2px;
  transition: grid-template-rows 0.2s;
}
.selected-ingredients-menu_active .selected-ingredients-menu__arrow-down-icon {
  transform: rotateX(180deg);
  transition: transform 0.2s;
}

.selected-ingredients-menu__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.5rem;
  min-height: 35px;
}

.selected-ingredients-menu__arrow-down-icon {
  transition: transform 0.3s;
}

.selected-ingredients-menu__body {
  display: grid;
  grid-template-rows: 0fr;
  overflow: hidden;
  padding: 0 0.5rem;
  border-top: solid var(--color-bg-tertiary);
  border-width: 0;
  transition: grid-template-rows 0.2s, border 0s 0.2s;
}

.selected-ingredients-menu__content {
  overflow: hidden;
}

.selected-ingredients-menu__list {
  display: flex;
  flex-wrap: wrap;
  gap: var(--ingredients-gap);
  padding: 0.5rem 0;
  margin: 0;
  text-indent: 0;
  list-style-type: none;
}
.selected-ingredients-menu__list[empty] .selected-ingredients-menu__list-message {
  display: block;
}

.selected-ingredients-menu__item {
  display: flex;
  align-items: center;
  gap: 10px;
  min-height: 36px;
  padding: 0 0.75rem;
  border: 2px solid var(--color-white-400);
  border-radius: 18px;
  font: var(--font-type-body);
}
.selected-ingredients-menu__item-cross-icon {
  position: relative;
  top: 1px;
  fill: #fff;
}

.selected-ingredients-menu__list-message {
  display: none;
  margin: 0 auto;
  padding: 0;
  color: var(--color-text-secondary);
}

body {
  font: var(--font-type-body);
  color: var(--color-text-primary);
}

.main {
  min-height: 100svh;
  background: var(--color-bg-primary);
}

.wrapper {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 16px;
}

.content {
  display: grid;
  row-gap: 10px;
  padding-top: 100px;
}

.filters__button {
  display: grid;
  place-items: center;
  height: var(--button-height);
  aspect-ratio: 1/1;
  border: 0;
  padding: 0;
  background: var(--color-bg-tertiary);
  border-radius: var(--border-radius-200);
}

.filters__button-svg * {
  fill: #fff;
}

/* FILTERS */
.filters__header {
  position: sticky;
  top: 0;
  z-index: 10;
  padding: 1rem 1.5rem;
  background: var(--color-bg-secondary);
  font: var(--font-type-h3);
}

/* RECIPES */
.recipes {
  display: flex;
  flex-direction: column;
  row-gap: 0.75rem;
  width: 100%;
}

/* RECIPE CARD */
.recipe-card {
  width: 100%;
  background: var(--color-bg-secondary);
  border-radius: var(--border-radius-400);
}

.recipe-card__img-wrapper {
  height: 55.5vw;
  overflow: hidden;
  padding: 0.25rem;
  border-radius: var(--border-radius-400);
}

.recipe-card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: var(--border-radius-300);
}

.recipe-card__description {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  padding: 0.5rem 1rem;
}

.recipe-card__title {
  font: var(--font-type-h3);
  color: var(--color-text-secondary);
